// @flow
import React from 'react';
import autobind from 'autobind-decorator';
import Timestamp from 'react-timestamp';
import { AnchorButton, Position, Tooltip } from '@blueprintjs/core';
import _ from 'lodash';
import TileViewIcon from '../../style/icons/pages/dashboard/tile-view-icon.svg';
import ListViewIcon from '../../style/icons/pages/dashboard/list-view-icon.svg';
import TickIcon from '../../style/icons/pages/dashboard/tick-icon.svg';
// import UploadingIcon from '../../style/icons/pages/dashboard/uploading-icon.svg';
// import FileDoneIcon from '../../style/icons/pages/upload-file-folder-pages/folder-done-icon.svg';
import api from '../../common/api.js';
import Loading from './Loading.jsx';
// $FlowFixMe
import './ViewFiles.scss';

const VIEWS = {
  LIST_VIEW: 'listView',
  PREVIEW_VIEW: 'previewView',
};

type Props = {
  token: string,
  tokenType: string,
  selectFileCallback: any,
};

type State = {
  currentView: any,
  isLoading: boolean,
  fileList: Array<Object>,
  fileSelected: any,
};

export default class ViewFiles extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      currentView: VIEWS.PREVIEW_VIEW,
      isLoading: true,
      fileList: [],
      fileSelected: null,
    };
  }

  componentDidMount() {
    this.getFileList();
  }

  componentWillReceiveProps() {}

  @autobind
  getFileList() {
    this.setState({ isLoading: true });
    const { fileList } = this.state;
    // Query Mongo and get a list of files for the user.
    api
      .getFileListForUser(this.props.token, this.props.tokenType)
      .then((result) => {
        if (result.status === 200) {
          if (fileList.length !== result.data.length || result.data.length === 0) {
            this.state.isLoading = false;
            this.state.fileList = result.data;
            this.setState({ fileList: result.data });
            _.forEach(this.state.fileList, (file) => {
              file.isLoading = true;
              this._getFilePreview(file)
                .then((previewResult) => {
                  file.previewHTML = previewResult.data.content;
                  file.previewStyles = previewResult.data.styles;
                  file.isLoading = false;
                  this.forceUpdate();
                })
                .catch((err) => {
                  console.log(err);
                });
            });
          }
        } else {
          console.log('Err: ', result.statusText);
        }
      })
      .catch((err) => {
        console.log('Err: ', err);
      });
  }

  @autobind
  _getFilePreview(file: Object) {
    return new Promise((resolve) => {
      api.getFilePreviewForUser(this.props.token, this.props.tokenType, file._id).then((result) => {
        resolve(result);
      });
    });
  }

  @autobind
  _renderFileList() {
    const rows = [];
    // Assume all files are below 16mb.
    this.state.fileList.forEach((file, key) => {
      const validClass = 'valid';
      // If any files is over 16mb, it is invalid and they cannot proceed.
      rows.push(
        <div className="row" key={`row-${key}`}>
          <TickIcon className="tickIcon" />
          <span className="fileName">{file.name}</span>
          <span className={`fileSize ${validClass}`}>
            <Timestamp time={file.uploadedAt} format="full" />
          </span>
        </div>,
      );
    });
    return rows;
  }

  @autobind
  _renderFilePreview() {
    const cells = [];
    // Assume all files are below 16mb.
    this.state.fileList.forEach((file, key) => {
      console.log('Rendering Preview: ', file);
      const validClass = 'valid';
      const loadingClass = file.loading;
      let selectedClass = '';
      if (this.state.fileSelected === key) {
        selectedClass = 'selected';
      }
      // If any files is over 16mb, it is invalid and they cannot proceed.
      cells.push(
        <div className="Grid-cell" key={`cell-${key}`}>
          <div
            onClick={() => {
              this._selectFileAtIndex(key);
            }}
            className={`filePreview ${validClass}`}
          >
            <TickIcon className="tickIcon" />
            <Tooltip content={file.name} position={Position.TOP}>
              {file.isLoading && <Loading isFullScreen={false} />}
              {!file.isLoading // TEXT FILE
                && file.mimetype === 'text/plain' && (
                  <div
                    readOnly
                    disabled
                    className={`docPreview  txt ${selectedClass} loading_${loadingClass}`}
                    dangerouslySetInnerHTML={{ __html: file.previewHTML }}
                  />
              )}
              {!file.isLoading // PDF FILE
                && file.mimetype === 'application/pdf' && (
                  <div
                    className={`docPreview pdf ${selectedClass} loading_${loadingClass}`}
                    dangerouslySetInnerHTML={{ __html: file.previewHTML }}
                  />
              )}
              {!file.isLoading // EVERYTHING ELSE
                && file.mimetype !== 'text/plain'
                && file.mimetype !== 'application/pdf' && (
                  <div
                    style={file.previewStyles}
                    className={`docPreview default ${selectedClass} loading_${loadingClass}`}
                    dangerouslySetInnerHTML={{ __html: file.previewHTML }}
                  />
              )}
            </Tooltip>
          </div>
          <span className="fileDate">
            <Timestamp time={file.uploadedAt} format="date" />
            <Timestamp time={file.uploadedAt} format="time" />
          </span>
        </div>,
      );
    });
    return <div className="Grid">{cells}</div>;
  }

  @autobind
  _selectFileAtIndex(index: number) {
    this.props.selectFileCallback(this.state.fileList[index]);
    this.setState({ fileSelected: index });
  }

  render() {
    const { isLoading } = this.state;
    if (isLoading) {
      return (
        <div className="subWrapper">
          <div className="contentWrapper">
            <div className="fileSummaryListWrapper">
              <div className="fileList">
                <div className="row header">
                  <Loading isFullScreen={false} />
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }

    if (this.state.fileList.length === 0) {
      return (
        <div className="subWrapper">
          <div className="contentWrapper">
            <div className="fileSummaryListWrapper">
              <div className="messageWrapper">
                <span className="messageHeader">Uploaded Documents: </span>
                <span className="message">
                  No documents found. Upload a document or IPFS link using the upload tab.{' '}
                </span>
              </div>
            </div>
          </div>
        </div>
      );
    }
    return (
      <div className="subWrapper">
        <div className="contentWrapper">
          <div className="fileSummaryListWrapper">
            <div className="fileList">
              <div className="row header">
                <span className="headerLabel">Recently Uploaded:</span>
                <div className="headerButtons">
                  <AnchorButton
                    className={`iconButton tileViewIcon ${(
                      this.state.currentView === VIEWS.PREVIEW_VIEW
                    ).toString()}`}
                    onClick={() => {
                      this.setState({ currentView: VIEWS.PREVIEW_VIEW });
                    }}
                  >
                    <TileViewIcon />
                  </AnchorButton>
                  <AnchorButton
                    className={`iconButton listViewIcon ${(
                      this.state.currentView === VIEWS.LIST_VIEW
                    ).toString()}`}
                    onClick={() => {
                      this.setState({ currentView: VIEWS.LIST_VIEW });
                    }}
                  >
                    <ListViewIcon />
                  </AnchorButton>
                </div>
              </div>
              {this.state.currentView === VIEWS.LIST_VIEW && this._renderFileList()}
              {this.state.currentView === VIEWS.PREVIEW_VIEW && this._renderFilePreview()}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
