import _ from 'lodash';
import axios from 'axios';

export default {
  getAllUsers: () => axios.get('/api/users'),
  getUser: userId => axios.get(`/api/user/${userId}`),
  checkUserToken: (token, userId, type) => axios.get(`/api/userToken/${type}/${userId}/${token}`),
  getFileListForUser: (token, tokenType) => axios.get(`/api/fileList/${tokenType}/${token}`),
  uploadFiles: (fileData, token, tokenType, progressCallback) => {
    const fd = new FormData();
    _.forEach(fileData, (file) => {
      if (file) {
        fd.append('files[]', file, file.originalname);
      }
    });
    fd.append('token', token);
    fd.append('tokenType', tokenType);
    const config = {
      onUploadProgress: (progressEvent) => {
        progressCallback(progressEvent.loaded / progressEvent.total);
      },
    };
    return axios.post('/api/upload', fd);
  },
  getFilePreviewForUser: (token, tokenType, fileId) => axios.get(`/api/filePreview/${tokenType}/${token}/${fileId}`),
  getFullFileForUser: (fileId, token) => axios.get(`/api/fullFilePreview/${token}/${fileId}`),
};
