import _ from 'lodash';

export const convertBytes = (value: any, unit: string, length: number) => {
  const result = {
    value,
    unit,
  };
  if (String(Math.round(value)).length > length) {
    switch (unit) {
      case 'b':
        result.unit = 'kb';
        break;
      case 'kb':
        result.unit = 'mb';
        break;
      case 'mb':
        result.unit = 'gb';
        break;
      case 'gb':
        result.unit = 'tb';
        break;
      case 'tb':
        result.unit = 'pb';
        break;
      case 'pb':
        result.unit = 'eb';
        break;
      case 'b/s':
        result.unit = 'kb/s';
        break;
      case 'kb/s':
        result.unit = 'mb/s';
        break;
      case 'mb/s':
        result.unit = 'gb/s';
        break;
      case 'gb/s':
        result.unit = 'tb/s';
        break;
      default: {
        console.error(`"${unit}" is the maximum supported unit of bytes.`);
        return result;
      }
    }

    result.value = parseFloat(value) / 1024;
    return convertBytes(result.value, result.unit, length);
  }
  result.value = _.round(parseFloat(value), 2);
  return result;
};
