import React from 'react';
import queryString from 'query-string';
import { withRouter, Redirect } from 'react-router';
import Register from '../Register/Register.jsx';
import TopNavBar from '../Navigation/TopNavBar.jsx';
import SideNavBar from '../Navigation/SideNavBar.jsx';
import Footer from '../Footer/Footer.jsx';
import { PAGES } from '../../common/constants.js';
import { checkToken } from '../../common/authentication.js';

type State = { loggedIn: boolean };
type Props = { location: any, history: any };
class RegisterationPage extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      loggedIn: false,
    };
  }

  componentWillMount() {}

  componentDidMount() {
    console.log(this.props);
    if (this.props.location && this.props.location.search) {
      const query = queryString.parse(this.props.location.search);
      if (query.token) {
        window.localStorage.setItem('proven_docs_token', query.token);
        this.props.history.push('/login');
      }
    }

    // Check logged in.
    if (window.localStorage.getItem('proven_docs_token')) {
      checkToken(window.localStorage)
        .then((response) => {
          if (response.status === 200) {
            console.log('Logged in!');
            this.setState({ loggedIn: true });
          } else if (response.response.status === 400) {
            console.log('Token Expired, Please log in again.');
            this.setState({ loggedIn: false });
          }
        })
        .catch((err) => {
          if (err.response.status === 400) {
            // Invalid Token, log in again.
            console.log('Please log in again.');
            this.setState({ loggedIn: false });
          }
        });
    }
  }

  componentWillReceiveProps() {}

  render() {
    if (this.state.loggedIn) {
      return <Redirect to="/secureDashboard" />;
    }

    if (this.state.loggedIn) {
      // Already logged in, redirect to dashboard.
      return (
        <div className="App">
          <TopNavBar currentPage={PAGES.REGISTER} />
          <div className="AppBody">
            <SideNavBar />
            <div className="mainPanel">
              <h2> Logged In </h2>
            </div>
          </div>
          <Footer currentPage={PAGES.REGISTER} />
        </div>
      );
    }

    return (
      <div className="App">
        <TopNavBar currentPage={PAGES.REGISTER} />
        <div className="AppBody">
          <div className="mainPanel">
            <Register pageProps={this.props} />
          </div>
        </div>
        <Footer currentPage={PAGES.REGISTER} />
      </div>
    );
  }
}

export default withRouter(RegisterationPage);
