import React from 'react';
import autobind from 'autobind-decorator';
import TopNavBar from '../Navigation/TopNavBar.jsx';
import TabbedPanel from '../Dashboard/TabbedPanel.jsx';
import { PAGES } from '../../common/constants.js';
import { checkToken } from '../../common/authentication.js';
import Loading from '../Dashboard/Loading.jsx';
import ViewDocsIcon from '../../style/icons/pages/dashboard/view-documents-icon.svg';
import UploadIcon from '../../style/icons/pages/dashboard/upload-icon.svg';
import ArrowIcon from '../../style/icons/pages/upload-file-folder-pages/arrow.svg';
import HistoryIcon from '../../style/icons/pages/dashboard/history-view-icon.svg';
import EmailIcon from '../../style/icons/pages/dashboard/email-icon.svg';
import LinkIcon from '../../style/icons/pages/dashboard/link-icon.svg';
import ViewProofIcon from '../../style/icons/pages/dashboard/view-proof-icon.svg';
import PreviewDocumentIcon from '../../style/icons/pages/dashboard/preview-document-icon.svg';
import NewFileUpload from '../Dashboard/NewFileUpload.jsx';
import ViewFiles from '../Dashboard/ViewFiles.jsx';
import ViewDocument from '../ViewDocument/ViewDocument.jsx';
import ViewProof from '../ViewProof/ViewProof.jsx';
// $FlowFixMe
import './Dashboard.scss';

const LHS_TABS = {
  VIEW_DOCUMENTS: 'viewDocs',
  NEW_UPLOAD: 'uploadDoc',
};
const RHS_TABS = {
  VIEW_PROOF: 'viewProof',
  VIEW_DOCUMENT: 'viewDoc',
};

export const RHS_STAGES = {
  BEGIN: 'begin',
  FILES_EXCEEDING: 'files_exceeding',
  DOCUMENT_MATCHING: 'document_matching',
  LOADING: 'loading',
  SELECT_FILE: 'selectFile',
  FILE_PREVIEW: 'filePreview',
};

type Props = { history: any };
type State = {
  storageUsed: number,
  storageFree: number,
  lhsTabSelected: string,
  rhsTabSelected: string,
  token: string,
  tokenType: string,
  fileSelected: Object,
  rhsStage: string,
};

export default class Dashboard extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      storageUsed: 0,
      storageFree: 1000000,
      lhsTabSelected: LHS_TABS.VIEW_DOCUMENTS,
      rhsTabSelected: RHS_TABS.VIEW_DOCUMENT,
      token: null,
      tokenType: null,
      fileSelected: null,
      rhsStage: RHS_STAGES.BEGIN,
    };

    this.lhsTabExtras = (
      <div className="fileSizeInfo">
        <span className="used">
          <b>Used: </b>
          {this.state.storageUsed}
        </span>
        <div className="vr" />
        <span className="free">
          <b>Free: </b>
          {this.state.storageFree}
        </span>
      </div>
    );

    this.rhsTabExtras = (
      <div className="rhsExtras">
        <HistoryIcon className="historyIcon" />
        <div className="vr" />
        <EmailIcon className="emailIcon" />
        <div className="vr" />
        <LinkIcon className="linkIcon" />
      </div>
    );
  }

  componentDidMount() {
    if (window.localStorage.getItem('proven_docs_token')) {
      checkToken(window.localStorage)
        .then((response) => {
          if (response.status === 200) {
            console.log('Logged in!');
            this.setState({ token: window.localStorage.getItem('proven_docs_token') });
            this.setState({ tokenType: window.localStorage.getItem('proven-docs-token-type') });
          } else if (response.response.status === 400) {
            console.log('Token Expired, Please log in again.');
            this.props.history.push('/login');
          }
        })
        .catch((err) => {
          console.log('Please log in again.');
          this.props.history.push('/login');
        });
    }

    // OFFLINE HACK
    // this.setState({ token: window.localStorage.getItem('proven_docs_token') });
    // this.setState({ tokenType: window.localStorage.getItem('proven-docs-token-type') });
  }

  componentWillReceiveProps() {}

  @autobind
  _swapLHSTab() {
    if (this.state.lhsTabSelected === LHS_TABS.VIEW_DOCUMENTS) {
      this.setState({ lhsTabSelected: LHS_TABS.NEW_UPLOAD });
    } else {
      this.setState({ lhsTabSelected: LHS_TABS.VIEW_DOCUMENTS });
    }
    this.refs.viewDocs.getFileList();
  }

  @autobind
  _swapRHSTab() {
    if (this.state.rhsTabSelected === RHS_TABS.VIEW_PROOF) {
      this.setState({ rhsTabSelected: RHS_TABS.VIEW_DOCUMENT });
    } else {
      this.setState({ rhsTabSelected: RHS_TABS.VIEW_PROOF });
    }
  }

  @autobind
  _fileSelected(file: Object) {
    this.setState({ fileSelected: file });
  }

  @autobind
  _setTab(newTabId: string) {
    this.setState({ lhsTabSelected: newTabId });
  }

  @autobind
  _setRHSTab(newTabId: string) {
    this.setState({ rhsTabSelected: newTabId });
  }

  @autobind
  _setStage(newStage: string) {
    console.log('Setting Stage: ', newStage);
    this.setState({ rhsStage: newStage });
  }

  _renderViewDocumentsRHS() {
    return <div />;
  }

  _renderNewUploadRHS() {
    const { rhsStage } = this.state;
    console.log('RHS State: ', rhsStage);
    switch (rhsStage) {
      case RHS_STAGES.BEGIN:
        return (
          <div className="newUploadRHSWrapper">
            <div className="centerContent">
              <ArrowIcon className="arrowIcon" />
              <span className="arrowText">Select or upload a document to begin.</span>
            </div>
          </div>
        );
      case RHS_STAGES.FILES_EXCEEDING:
        return (
          <div className="newUploadRHSWrapper">
            <div className="centerContent">
              <span className="arrowText">
                Sorry, the highlighted files exceed your upload limit of 16 MB. Please remove these
                files to continue to upload.
              </span>
            </div>
          </div>
        );
      case RHS_STAGES.DOCUMENT_MATCHING:
        return (
          <div className="newUploadRHSWrapper">
            <div className="centerContent">
              <span className="arrowText">
                We have noticed this document already exists. Would you like to upload an updated
                version of this file?
              </span>
            </div>
          </div>
        );
      case RHS_STAGES.LOADING:
        return (
          <div className="newUploadRHSWrapper">
            <div className="centerContent">
              <Loading isFullScreen={false} />
            </div>
          </div>
        );
      default:
        return <div />;
    }
  }

  render() {
    if (this.state.token === null) {
      return (
        <div className="App">
          <div className="AppBody">
            <div className="mainPanel">
              <Loading isFullScreen />
            </div>
          </div>
        </div>
      );
    }
    const {
      token, tokenType, lhsTabSelected, rhsTabSelected, fileSelected,
    } = this.state;

    const lhsTabs = [
      {
        id: 'viewDocs',
        icon: <ViewDocsIcon />,
        panel: (
          <div className="wrapper">
            <ViewFiles
              token={token}
              tokenType={tokenType}
              ref="viewDocs"
              swapTabCallback={this._swapLHSTab}
              selectFileCallback={this._fileSelected}
            />
          </div>
        ),
      },
      {
        id: 'uploadDoc',
        icon: <UploadIcon />,
        panel: (
          <div className="wrapper">
            <NewFileUpload token={token} tokenType={tokenType} setStageCallback={this._setStage} swapTabCallback={this._swapLHSTab} />
          </div>
        ),
      },
    ];

    const rhsTabs = [
      {
        id: 'viewProof',
        icon: <ViewProofIcon />,
        panel: (
          <div className="wrapper">
            <ViewProof
              token={token}
              tokenType={tokenType}
              ref="viewProof"
              file={fileSelected}
              swapTabCallback={this._swapRHSTab}
              selectFileCallback={this._fileSelected}
            />
          </div>
        ),
      },
      {
        id: 'viewDoc',
        icon: <PreviewDocumentIcon />,
        panel: (
          <div className="wrapper">
            <ViewDocument
              token={token}
              tokenType={tokenType}
              file={fileSelected}
              setStageCallback={this._setStage}
            />
          </div>
        ),
      },
    ];

    return (
      <div className="App">
        <TopNavBar currentPage={PAGES.DASHBOARD} />
        <div className="AppBody">
          <div className="mainPanel">
            <div className="timeLine" />
            <div className="lowerGroup">
              <div className="lhs">
                <TabbedPanel
                  className="lhsTabbedPanel"
                  tabs={lhsTabs}
                  extraComponents={this.lhsTabExtras}
                  tabSelected={lhsTabSelected}
                  setTabCallback={this._setTab}
                />
              </div>
              <div className="vr" />
              <div className="rhs">
                {lhsTabSelected === LHS_TABS.VIEW_DOCUMENTS && (
                  <TabbedPanel
                    className="lhsTabbedPanel"
                    tabs={rhsTabs}
                    extraComponents={this.rhsTabExtras}
                    tabSelected={rhsTabSelected}
                    setTabCallback={this._setRHSTab}
                  />
                )}
                {lhsTabSelected === LHS_TABS.NEW_UPLOAD && this._renderNewUploadRHS()}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
