import React from 'react';
import { withRouter, Redirect } from 'react-router';
import queryString from 'query-string';
import { Overlay } from '@blueprintjs/core';
import Login from '../Login/Login.jsx';
import RegisterSuccess from '../Register/RegisterSuccess.jsx';
import RegisterFailed from '../Register/RegisterFailed.jsx';
import TopNavBar from '../Navigation/TopNavBar.jsx';

import SideNavBar from '../Navigation/SideNavBar.jsx';
import Footer from '../Footer/Footer.jsx';

import { PAGES } from '../../common/constants.js';
import { checkToken } from '../../common/authentication.js';

type Props = {
  loggedOut: boolean,
  location: any,
  signedUp: boolean,
  signUpFailed: boolean,
  failedLogin: boolean,
  success: boolean,
};
type State = { loggedIn: boolean, checkingStatus: boolean };
class LoginPage extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      loggedIn: false,
      checkingStatus: true,
    };
  }

  componentWillMount() {
    if (this.props.loggedOut) {
      console.log('Logged out, removing tokens...');
      window.localStorage.removeItem('proven-docs-token');
      window.localStorage.removeItem('proven-docs-token-type');
      window.localStorage.setItem('proven_docs_token', null);
      window.localStorage.setItem('proven_docs_token_type', null);
    }

    if (this.props.location && this.props.location.search) {
      const query = queryString.parse(this.props.location.search);
      if (query.token && query.type) {
        window.localStorage.setItem('proven_docs_token', query.token);
        window.localStorage.setItem('proven_docs_token_type', query.type);
      }
    }

    // Check logged in.
    if (window.localStorage.getItem('proven_docs_token') && !this.props.signedUp) {
      checkToken(window.localStorage)
        .then((response) => {
          if (response.status === 200) {
            console.log('Logged in!');
            this.setState({ checkingStatus: false });
            this.setState({ loggedIn: true });
          } else if (response.response.status === 400) {
            console.log('Token Expired, Please log in again.');
            this.setState({ checkingStatus: false });
            this.setState({ loggedIn: false });
          }
        })
        .catch((err) => {
          if (err.response.status === 400) {
            // Invalid Token, log in again.
            console.log('Please log in again.');
            this.setState({ loggedIn: false });
          }
        });
    }
  }

  componentDidMount() {}

  componentWillReceiveProps() {}

  render() {
    if (this.state.loggedIn) {
      return <Redirect to="/secureDashboard" />;
    }

    if (this.state.loggedIn) {
      // Already loggen in, redirect to dashboard.
      return (
        <div className="App">
          <TopNavBar currentPage={PAGES.LOGIN} />
          <div className="AppBody">
            <SideNavBar />
            <div className="mainPanel">
              <h2> Logged In </h2>
            </div>
          </div>
          <Footer currentPage={PAGES.LOGIN} />
        </div>
      );
    }

    if (this.props.signedUp) {
      return (
        <div className="App">
          <TopNavBar currentPage={PAGES.LOGIN} />
          <div className="AppBody">
            <div className="mainPanel">
              <RegisterSuccess />
            </div>
          </div>
          <Footer currentPage={PAGES.LOGIN} />
        </div>
      );
    }

    if (this.props.signUpFailed) {
      return (
        <div className="App">
          <TopNavBar currentPage={PAGES.LOGIN} />
          <div className="AppBody">
            <div className="mainPanel">
              <RegisterFailed />
            </div>
          </div>
          <Footer currentPage={PAGES.LOGIN} />
        </div>
      );
    }

    return (
      <div className="App">
        <TopNavBar currentPage={PAGES.LOGIN} />
        <div className="AppBody">
          <div className="mainPanel">
            <Login
              pageProps={this.props}
              failedLogin={this.props.failedLogin}
              success={this.props.success}
            />
          </div>
        </div>
        <Footer currentPage={PAGES.LOGIN} />
      </div>
    );
  }
}

export default withRouter(LoginPage);
