import React from 'react';
import { Link } from 'react-router-dom';
import LogoIcon from '../../style/icons/pages/top-nav-bar/logo.svg';
import { PAGES } from '../../common/constants.js';
// $FlowFixMe
import './TopNavBar.scss';

type Props = { currentPage: string | null };
type State = { currentPage: string | null };
export default class TopNavBar extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      currentPage: null,
    };
  }

  componentDidMount() {
    this.setState({ currentPage: this.props.currentPage });
  }

  render() {
    return (
      <div className="topNavigationRoot">
        <div className="leftButtons">
          <div className="appLogo" id="home">
            <Link
              className="logoLink homeLink"
              to={{ pathname: '/', search: '' }}
              style={{ textDecoration: 'none' }}
            >
              <LogoIcon />
            </Link>
          </div>
          {this.state.currentPage === PAGES.HOME && (
            <div className="homeLinks">
              <div className="vr" />
              <a href="#home" className="link homeLink" style={{ textDecoration: 'none' }}>
                <span className="homeLabel">Home</span>
              </a>
              <a href="#about" className="link aboutLink" style={{ textDecoration: 'none' }}>
                <span className="aboutLabel">About</span>
              </a>
            </div>
          )}
        </div>
        <div className="rightButtons">
          {this.state.currentPage === PAGES.HOME && (
            <div className="homeButtons">
              <div className="vr" />
              <Link
                className=" button loginButton"
                to={{ pathname: '/login', search: '' }}
                style={{ textDecoration: 'none' }}
              >
                <span className=" button-text">Log In</span>
              </Link>
              <Link
                className=" button signupButton"
                to={{ pathname: '/signup', search: '' }}
                style={{ textDecoration: 'none' }}
              >
                <span className=" button-text">Sign Up</span>
              </Link>
            </div>
          )}
          {(this.state.currentPage === PAGES.LOGIN
            || this.state.currentPage === PAGES.REGISTER) && (
            <Link
              className=" button homeButton"
              to={{ pathname: '/', search: '' }}
              style={{ textDecoration: 'none' }}
            >
              <span className=" button-text">Home</span>
            </Link>
          )}
          {this.state.currentPage === PAGES.DASHBOARD && (
            <div className="homeButtons">
              <div className="vr" />
              <Link
                className=" button loginButton"
                to={{ pathname: '/logout', search: '' }}
                style={{ textDecoration: 'none' }}
              >
                <span className=" button-text">Log out</span>
              </Link>
            </div>
          )}
        </div>
      </div>
    );
  }
}
