import React from 'react';
import autobind from 'autobind-decorator';
import { AnchorButton } from '@blueprintjs/core';
import _ from 'lodash';
import FileIcon from '../../style/icons/pages/upload-file-folder-pages/folder-icon.svg';
// import FileDoneIcon from '../../style/icons/pages/upload-file-folder-pages/folder-done-icon.svg';
import api from '../../common/api.js';
import { convertBytes } from '../../common/util.js';
import Loading from './Loading.jsx';
import './NewUpload.scss';

const STAGES = {
  BEGIN: 'begin',
  FILES_EXCEEDING: 'files_exceeding',
  DOCUMENT_MATCHING: 'document_matching',
  LOADING: 'loading',
  FILE_SUMMARY: 'fileSummary',
  CONFLICT_RESOLUTION: 'conflictResolution',
};

export default class NewFileUpload extends React.Component {
  constructor() {
    super();
    this.state = {
      currentStage: STAGES.BEGIN,
      fileList: [],
      totalFileSize: 0,
      filesAreAllValid: true,
    };
  }

  componentDidMount() {}

  componentWillReceiveProps() {}

  @autobind
  _onClickCancel() {
    this.setState({ currentStage: STAGES.BEGIN });
    this.props.setStageCallback(STAGES.BEGIN);
    this.setState({ fileList: [] });
    this.setState({ totalFileSize: 0 });
  }

  @autobind
  _onClickUpload() {
    console.log('Upload files: ', this.state.fileList);
    this.setState({ currentStage: STAGES.LOADING });
    this.props.setStageCallback(STAGES.LOADING);
    api
      .uploadFiles(
        this.state.fileList,
        this.props.token,
        this.props.tokenType,
        this._updateUploadProgress,
      )
      .then((result) => {
        console.log('Finished uploading...');
        this._onClickCancel();
        this.props.swapTabCallback();
      })
      .catch((err) => {
        console.log('Error uploading files: ', err);
      });
  }

  @autobind
  _onFileChange(event) {
    const { files } = event.target;
    let fileSizeSum = 0;
    this.setState({ totalFileSize: 0 });

    _.forIn(files, (value, key) => {
      // Iterates through the file list object by key.
      if (key !== 'length' && key !== 'item') {
        // Only push file items.
        this.state.fileList.push(value); // Add file to the list.
        fileSizeSum += value.size;
      }
      if (key == files.length - 1) {
        this.setState({ totalFileSize: fileSizeSum });
        this.setState({ currentStage: STAGES.FILE_SUMMARY }); // Done.
      }
    });
  }

  @autobind
  _removeFile(index) {
    const { totalFileSize, fileList } = this.state;
    const { size } = this.state.fileList[index];
    const newTotalFileSize = totalFileSize - size;

    fileList.splice(index, 1);
    this.setState({ totalFileSize: newTotalFileSize });

    // If there are no files left, return to upload widget.
    if (fileList.length === 0) {
      this._onClickCancel();
    }
  }

  @autobind
  _renderFileList() {
    const rows = [];
    // Assume all files are below 16mb.
    this.state.fileList.forEach((file, key) => {
      const validClass = 'valid';
      const result = convertBytes(file.size, 'b', 3);
      // If any files is over 16mb, it is invalid and they cannot proceed.
      rows.push(
        <div className="row">
          <span className="fileName">{file.name}</span>
          <span className={`fileSize ${validClass}`}>
            {result.value} {result.unit}
          </span>
          <AnchorButton
            onClick={() => {
              this._removeFile(key);
            }}
            className="removeButton"
          >
            x
          </AnchorButton>
        </div>,
      );
    });
    return rows;
  }

  @autobind
  _updateUploadProgress(progress) {
    if (progress) {
      console.log(progress);
    }
  }

  render() {
    const result = convertBytes(this.state.totalFileSize, 'b', 3);

    switch (this.state.currentStage) {
      case STAGES.BEGIN:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="chooseFilesWrapper">
                <FileIcon />
                <h2 className="sectionTitle">File or Folder Upload</h2>
                <span className="sectionMessage">
                  Browser or drag and drop a file or folder to begin
                </span>
                <div className="inputWrapper whiteButton">
                  <input
                    className="whiteButton browseFilesButton"
                    name="files"
                    type="file"
                    onChange={this._onFileChange}
                    multiple
                  />
                  <span className="inputLabel">Browse Files</span>
                </div>
                <span className="fileSizeMessage">Maximum upload file size: 16 MB</span>
              </div>
            </div>
            <div className="footerButtons">
              <div className="buttons">
                <AnchorButton
                  text="IPFS Placeholder"
                  className="cancelButton whiteButton"
                  onClick={this._onClickCancel}
                />
              </div>
            </div>
          </div>
        );
      case STAGES.FILE_SUMMARY:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="fileSummaryListWrapper">
                <div className="fileList">
                  <div className="row header">
                    <span className="headerLabel">Upload Overview:</span>
                    <span className="totalLabel">
                      Total: {result.value} {result.unit}
                    </span>
                  </div>
                  {this._renderFileList()}
                </div>
              </div>
            </div>
            <div className="footerButtons">
              <div className="buttons">
                <AnchorButton
                  text="Cancel"
                  className="cancelButton blueButton"
                  onClick={this._onClickCancel}
                />
                <AnchorButton
                  text="Upload"
                  className="uploadButton whiteButton"
                  disabled={!this.state.filesAreAllValid}
                  onClick={this._onClickUpload}
                />
              </div>{' '}
            </div>
          </div>
        );
      case STAGES.LOADING:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="fileSummaryListWrapper">
                <div className="fileList">
                  <div className="row header">
                    <Loading isFullScreen={false} />
                  </div>
                </div>
              </div>
            </div>
            <div className="footerButtons">
              <div className="buttons">
                <AnchorButton
                  text="Cancel"
                  className="cancelButton blueButton"
                  onClick={this._onClickCancel}
                  disabled
                />
                <AnchorButton
                  text="Upload"
                  className="uploadButton whiteButton"
                  disabled={!this.state.filesAreAllValid}
                  onClick={this._onClickUpload}
                  disabled
                />
              </div>{' '}
            </div>
          </div>
        );
      default:
        return <div />;
    }
  }
}
