import React from 'react';
import { Link } from 'react-router-dom';
import { PAGES } from '../../common/constants.js';
// $FlowFixMe
import './Footer.scss';

type State = {};

type Props = { currentPage: String };
export default class TopNavBar extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {};
  }

  componentDidMount() {}

  render() {
    if (this.props.currentPage === PAGES.HOME) {
      return (
        <div className="footerRoot">
          <div className="spacer" />
          <span className="copyright">&copy; 2018 Southbank Software. All rights reserved.</span>
          <Link
            className="privacyLink"
            to={{ pathname: '/privacy', search: '' }}
            style={{ textDecoration: 'none' }}
          >
            <span className="privacy">Privacy Policy</span>
          </Link>
        </div>
      );
    }
    return (
      <div className="footerRoot light">
        <div className="spacer" />
        <span className="copyright">&copy; Southbank Software. All rights reserved.</span>
        <Link
          className="privacyLink"
          to={{ pathname: '/privacy', search: '' }}
          style={{ textDecoration: 'none' }}
        >
          <span className="privacy">Privacy Policy</span>
        </Link>
      </div>
    );
  }
}
