import React from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router';
import LockIcon from '../../style/icons/pages/login-signup-pages/log-in-icon.svg';
import CrossIcon from '../../style/icons/pages/login-signup-pages/error-icon.svg';
import GoogleIcon from '../../style/icons/pages/login-signup-pages/google-icon.svg';
import GithubIcon from '../../style/icons/pages/login-signup-pages/github-icon.svg';
import MicrosoftIcon from '../../style/icons/pages/login-signup-pages/microsoft-icon.svg';
import FacebookIcon from '../../style/icons/pages/login-signup-pages/facebook-icon.svg';
// $FlowFixMe
import './Login.scss';

type Props = { failedLogin: boolean };

type State = {
  hasToken: boolean,
};
export default class Login extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      hasToken: false,
    };
  }

  componentWillMount() {}

  componentDidMount() {}

  render() {
    if (this.state.hasToken) {
      return <Redirect to="/login" />;
    }
    return (
      <div className="loginPageRoot">
        <div className="loginCenter">
          {this.props.failedLogin ? (
            <div className="icon failedIcon">
              <CrossIcon />
            </div>
          ) : (
            <div className="icon loginIcon">
              <LockIcon />
            </div>
          )}
          {this.props.failedLogin ? (
            <div className="loginMessage">
              <span className="sectionTitle">Log In Failed!</span>
              <span className="sectionText">
                Could not log in with those credentials, please try again or sign up.
              </span>
            </div>
          ) : (
            <div className="loginMessage">
              <span className="sectionTitle">Log In</span>
              <span className="sectionText">Please log in with your prefered provider</span>
            </div>
          )}
          <a className=" oAuthButton googleButton button" href="/auth/google">
            <div className="button-text">
              <div className="icon googleIcon">
                <GoogleIcon />
              </div>
              <div className="vr" />
              <span>Log in with Google</span>
            </div>
          </a>
          <a className=" oAuthButton githubButton button" href="/auth/github">
            <div className="button-text">
              <div className="icon githubIcon">
                <GithubIcon />
              </div>
              <div className="vr" />
              <span>Log in with Github</span>
            </div>
          </a>
          <a className=" oAuthButton microsoftButton button" href="/auth/microsoft">
            <div className="button-text">
              <div className="icon microsoftIcon">
                <MicrosoftIcon />
              </div>
              <div className="vr" />
              <span>Log in with Microsoft</span>
            </div>
          </a>
          <a className=" oAuthButton facebookButton button" href="/auth/facebook">
            <div className="button-text">
              <div className="icon facebookIcon">
                <FacebookIcon />
              </div>
              <div className="vr" />
              <span>Log in with Facebook</span>
            </div>
          </a>
          <a className=" oAuthButton emailButton button" href="/auth/email">
            <div className="button-text">
              <span>Log in with Email</span>
            </div>
          </a>
          <div className="footerLinks">
            <Link
              className="forgotPasswordLink"
              to={{ pathname: '/forgotpassword', search: '' }}
              style={{ textDecoration: 'none' }}
            >
              <span className="forgotPassword">Forgot Password</span>
            </Link>
            <div className="vr" />
            <Link
              className="signUpLink"
              to={{ pathname: '/signup', search: '' }}
              style={{ textDecoration: 'none' }}
            >
              <span className="signUp">Sign Up</span>
            </Link>
            <br />
          </div>
        </div>
      </div>
    );
  }
}
