// eslint no-return-assign:0
import React from 'react';
import { Route, Switch } from 'react-router-dom';
import LoginPage from '../components/Pages/LoginPage.jsx';
import RegistrationPage from '../components/Pages/RegistrationPage.jsx';
import HomePage from '../components/Pages/HomePage.jsx';
import NotFound from '../components/Pages/404.jsx';
import Dashboard from '../components/Pages/Dashboard.jsx';
import '../style/global_styles.scss';

const Routes = () => (
  <Switch>
    <Route exact path="/" component={HomePage} />
    <Route exact path="/landing" component={HomePage} />
    <Route path="/logout" component={() => <LoginPage loggedOut />} />
    <Route path="/secureDashboard" component={Dashboard} />
    <Route exact path="/login" component={LoginPage} />
    <Route exact path="/loginFailed" component={() => <LoginPage failedLogin />} />
    <Route exact path="/loginSucceeded" component={() => <LoginPage success />} />
    <Route exact path="/signup" component={RegistrationPage} />
    <Route exact path="/signupFailed" component={() => <RegistrationPage failedSignup />} />
    <Route exact path="/signupSucceeded" component={() => <LoginPage signedUp />} />
    <Route path="/insta-redirect" component={() => (window.location = 'https://instagram.com')} />
    <Route path="/facebook-redirect" component={() => (window.location = 'https://facebook.com')} />
    <Route path="/twitter-redirect" component={() => (window.location = 'https://twitter.com')} />
    <Route path="*" component={NotFound} />
  </Switch>
);
export default Routes;
