import React from 'react';
import { Tab, Tabs } from '@blueprintjs/core';
import './TabbedPanel.scss';
import autobind from 'autobind-decorator';

export default class TabbedPanel extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  componentDidMount() {}

  componentWillReceiveProps() {}

  @autobind
  _onChange(newTabId, prevTabId) {
    this.props.setTabCallback(newTabId);
  }

  @autobind
  renderTabs() {
    const tabs = [];
    this.props.tabs.forEach((tab) => {
      tabs.push(
        <Tab id={tab.id} className={tab.title} title={tab.title || tab.icon} panel={tab.panel} />,
      );
    });
    return tabs;
  }

  render() {
    const className = this.props.className || 'tabs';
    return (
      <div className="tabbedPanelWrapper">
        <Tabs className={`${className} tabbedPanel`}  onChange={this._onChange} selectedTabId={this.props.tabSelected} defaultSelectedTabId={this.props.tabs[0].id}>
          {this.renderTabs()}
          <Tabs.Expander />
          <div className="extraTabContent">{this.props.extraComponents}</div>
        </Tabs>
      </div>
    );
  }
}
