// @flow
import Cookies from 'js-cookie';
import axios from 'axios';
import api from './api.js';
import { CLIENT_IDS } from './constants.js';

export const getAccessToken = () => Cookies.get('dbproof-access-token');
export const getRefreshToken = () => Cookies.get('refresh-token');

export const authenticate = async () => {
  Cookies.set('dbproof-access-token', true, { expires: 1 });
};

export const authenticateWithGoogle = async () => {};

export const checkGoogleToken = (token: string) => new Promise((resolve, reject) => {
  if (token && token !== 'null') {
    console.log('Token (google )exists! Checking against google...');
    axios
      .post(`https://www.googleapis.com/oauth2/v1/tokeninfo?access_token=${token}`)
      .then((response) => {
        if (response.status !== 200) {
          reject(response.statusText);
        } else {
          console.log('Valid Token, checking database...');
          api.checkUserToken(token, response.data.user_id, 'google').then((apiResponse) => {
            resolve(apiResponse);
          });
        }
      })
      .catch((err) => {
        reject(err);
      });
  } else {
    resolve(null);
  }
});

export const checkGithubToken = (token: string) => new Promise((resolve, reject) => {
  if (token && token !== 'null') {
    console.log('Token (Github) exists! Checking how long this token has existed...');

    console.log('Valid Token, checking database...');
    api
      .checkUserToken(token, null, 'github')
      .then((apiResponse) => {
        resolve(apiResponse);
      })
      .catch((err) => {
        console.log('err: ', err);
        reject(err);
      });
  } else {
    resolve(null);
  }
});

export const checkToken = (localStorage: Object) => {
  console.log(localStorage.getItem('proven_docs_token'));
  console.log(localStorage.getItem('proven_docs_token_type'));
  if (localStorage.getItem('proven_docs_token')) {
    switch (localStorage.getItem('proven_docs_token_type')) {
      case 'google':
        return checkGoogleToken(localStorage.getItem('proven_docs_token'));
      case 'github':
        return checkGithubToken(localStorage.getItem('proven_docs_token'));
      default:
        return checkGithubToken(localStorage.getItem('proven_docs_token'));
    }
  }
};
