import React from 'react';
import autobind from 'autobind-decorator';
import Loading from '../Dashboard/Loading.jsx';
import api from '../../common/api.js';
// $FlowFixMe
import './ViewDocument.scss';

const STATES = {
  SELECT_FILE: 'selectFile',
  LOADING: 'loading',
  FILE_PREVIEW: 'filePreview',
};

type Props = {
  file: Object,
  token: string,
};

type State = {
  file: any,
  filePreviewHTML: string | null,
  currentState: any,
};

export default class ViewDocument extends React.Component<Props, State> {
  constructor() {
    super();
    this.state = {
      file: null,
      currentState: STATES.SELECT_FILE,
      user: null,
      filePreviewHTML: null,
    };
  }

  componentWillReceiveProps(props: Object) {
    console.log('Props! ', props);
    const { file } = this.state;
    if (props.file !== null && props.file !== file) {
      this.state.file = props.file;
      this.setState({ currentState: STATES.LOADING });
      this._fetchFile()
        .then((data) => {
          console.log('File Information: ', file);
          console.log('File Data: ', data);
          // Create a public link for the file?
          this.setState({ filePreviewHTML: data.data.content });
          this.setState({ currentState: STATES.FILE_PREVIEW });
        })
        .catch((err) => {
          console.log('Err: ', err);
        });
    }
  }

  @autobind
  _fetchFile() {
    return new Promise((resolve, reject) => {
      const { token } = this.props;
      const { file } = this.state;
      api
        .getFullFileForUser(file._id, token)
        .then((result) => {
          console.log(result);
          resolve(result);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  render() {
    const { currentState, filePreviewHTML, file } = this.state;
    let previewClass = '';
    if (file) {
      console.log('type: ', file.mimetype);
      switch (file.mimetype) {
        case 'application/pdf':
          previewClass = 'pdf';
          break;
        default:
          previewClass = 'default';
          break;
      }
    }

    switch (currentState) {
      case STATES.SELECT_FILE:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="viewDocumentWrapper"> Please select a document.</div>
            </div>
          </div>
        );
      case STATES.LOADING:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <Loading isFullScreen={false} />
            </div>
          </div>
        );
      case STATES.FILE_PREVIEW:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="viewDocumentHeader">
                <div className="documentTitle">{file.name}</div>
              </div>
              {filePreviewHTML ? (
                <div className="viewDocumentWrapper">
                  <div
                    className={`${previewClass}`}
                    dangerouslySetInnerHTML={{ __html: filePreviewHTML }}
                  />
                </div>
              ) : (
                <div className="viewDocumentWrapper">Could not load preview.</div>
              )}
            </div>
          </div>
        );
      default:
        return (
          <div className="subWrapper">
            <div className="contentWrapper">
              <div className="viewDocumentWrapper"> View Document Placeholder.</div>
            </div>
          </div>
        );
    }
  }
}
